@keyframes mobileMenuEnter {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes mobileMenuExit {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-8px);
  }
}
.mobile-menu-enter {
  animation: mobileMenuEnter 220ms ease-out forwards;
}
.mobile-menu-exit {
  animation: mobileMenuExit 180ms ease-in forwards;
}
.scroll-reveal-hidden {
  opacity: 0;
  transform: translate3d(0, 26px, 0);
  transition: opacity 560ms cubic-bezier(0.22, 1, 0.36, 1),
    transform 560ms cubic-bezier(0.22, 1, 0.36, 1);
  transition-delay: var(--scroll-reveal-delay, 0ms);
  will-change: opacity, transform;
}
.scroll-reveal-hidden.scroll-reveal-visible {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
